import React from "react";

export class DiamondIcon extends React.Component {
	render() {
		return (
			<svg className="player-icon" id="Layer_1" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000">
				<path fill={this.props.color} d="M500,0,0,500l500,500,500-500Zm0,717.16L282.84,500,500,282.84,717.16,500Z" transform="translate(0 0)" />
			</svg>
		);
	}
}


